class CardHand extends Component {
    render() {
        const { deck_id } = this.props;

        return (
          <div>
            <button onClick={() => this.props.getCards({deck_id})}>Get Cards</button>
            <p>This will become the card hands: {deck_id}</p>
          </div>
        )
    }
}

export default CardHand
