import React, { Component } from 'react';
import CardHand from './CardHand';

class App extends Component {
    state = {
        data: []
    };

    componentDidMount() {
        // const url = "https://en.wikipedia.org/w/api.php?action=opensearch&search=Seona+Dancing&format=json&origin=*&limit=1";
        console.log("Look at me. I mounted!!")

        const url = 'https://deckofcardsapi.com/api/deck/new/shuffle/?deck_count=1'
        fetch(url)
            .then(response =>
              response.json()
            )
            .then(result => {
                console.log('BBBBBBB ', result)
                this.setState({
                    data: result
                })
            });
    }

    getCards(DeckId) {
      console.log('I am getting cards for deck: ', DeckId.deck_id)
    }

    render() {
        const {shuffled, deck_id} = this.state.data;
        console.log('11111', this.state)
        console.log('22222', this.state.data)
        console.log('Shuffled ', shuffled)
        console.log('Deck ID: ', deck_id)
        // const result = data.map((entry, index) => {
        //     console.log('xxxxxxxxxxxxx', entry);
        //     return <li key={index}>{entry}</li>;
        // });

        return (
            <div className="container">
                <h1>Cribbage Hand Tester</h1>
                <p>Guess how many points this hand is worth.</p>
                <CardHand deck_id={deck_id} getCards={this.getCards}/>
            </div>
        );
    }
}

export default App;
